<template>
<div>
  <h3>筛选递归组件，用于筛选部门/人员等</h3>
  <checkbox-tree :nodes="treeNode"></checkbox-tree>
</div>
</template>
<script>
export default {
  data () {
    return {
      // 可拖拽排序列表数据
      treeNode: [{
        name: '0',
        checked: true
      }, {
        name: '1',
        children: [{
          name: '1-0',
          children: [{
            name: '1-0-0',
            children: [{
              name: '1-0-0-0',
              checked: true
            }, {
              name: '1-0-0-1'
            }]
          }, {
            name: '1-0-1',
            children: [{
              name: '1-0-1-0',
              children: [{
                name: '1-0-1-0-1'
              }]
            }, {
              name: '1-0-1-1',
              checked: true
            }, {
              name: '1-0-1-2',
              checked: true
            }]
          }]
        }]
      }, {
        name: '2',
        children: [{
          name: '2-0'
        }]
      }]
    }
  },
  mounted () {
    this.initData(this.treeNode)
  },
  methods: {
    initData (nodes) {
      nodes.forEach(el => {
        if (!el.hasOwnProperty('checked')) {
          el = Object.assign({}, el, {
            checked: false
          })
        }
        el.children 
          && el.children.length 
          && this.initData(el.children)
      })
      debugger
    }
  },
  components: {
    'checkbox-tree': require('../components/checkbox-tree/index')
  }
}
</script>


