<template>
  <li>
    <label>
      <input type="checkbox" :checked="treeNode.checked"
              @change="toggle($event)">
      <span>{{treeNode.name}}</span>
    </label>
    <ul class="tree-list">
      <tree v-if="treeNode.children && treeNode.children.length"
            v-for="node in treeNode.children"
            :tree-node="node"
            @update-checked="updateChecked"></tree>
    </ul>
  </li>
</template>
<script>
export default {
  name: 'tree',
  props: ['treeNode'],
  data () {
    return {

    }
  },
  methods: {
    toggle (e) {
      // 保存当前操作节点值
      let checked = e.currentTarget.checked
      // 向祖先元素递归
      this.checkParent(checked)
      // 向子元素递归
      this.treeNode.children 
        && this.treeNode.children.length 
        && this.checkChildren(this.treeNode.children, checked)
    },
    checkParent (checked) {
      // 向父元素传递当前节点以及元素的变化信息
      this.$emit('update-checked', this.treeNode, [{node: this.treeNode.name, checked: checked}])
    },
    updateChecked (node, newNode) {
      // 根据子元素获取当前元素的变化信息，继续向祖先元素递归
      var bool = this.treeNode.children.filter(el => el !== node).every(el => el.checked) && newNode[0].checked
      newNode.unshift({node: this.treeNode.name, checked: bool})
      this.$emit('update-checked', this.treeNode, newNode)
    },
    checkChildren (nodes, checked) {
      this.downUpdate(nodes, checked)
    },
    downUpdate (nodes, bool) {
      // 把所有子元素的选中状态对齐当前元素
      nodes.forEach((el, idx) => {
        nodes.splice(idx, 1, Object.assign({}, el, {checked: bool}))
        el.children
          && el.children.length
          && this.downUpdate(el.children, bool)
      })
    }
  }
}
</script>
<style scoped>
.tree-list{padding-left: 15px;}
.tree-list li{line-height: 1.5;}
.tree-list input{display: inline;  width: auto; height: auto;}
.tree-list label{display: inline-block;}
</style>


