<template>
<div>
<ul class="tree-list">
  <tree v-if="treeNode"
        v-for="node in treeNode"
        :tree-node="node"
        @update-checked="updateChecked"></tree>
</ul>
</div>
</template>
<script>
export default {
  props: ['nodes'],
  data () {
    return {

    }
  },
  // mounted () {
  //   this.initData(this.treeNode)
  // },
  methods: {
    // initData (nodes) {
    //   nodes.forEach(el => {
    //     if (!el.hasOwnProperty('checked')) {
    //       el = Object.assign({}, el, {
    //         checked: false
    //       })
    //     }
    //     el.children 
    //       && el.children.length 
    //       && this.initData(el.children)
    //   })
    // },
    updateChecked (node, newNode) {
      // 更新变化
      this.checkChangedNodes(this.nodes, newNode);
    },
    checkChangedNodes (nodes, filterNodes) {
      // 找出当前变化元素，更新选中状态
      let idx = nodes.findIndex(el => el.name === filterNodes[0].node)
      let bool = filterNodes[0].checked
      // console.log(idx)
      nodes.splice(idx, 1, Object.assign({}, nodes[idx], {checked: bool}))
      filterNodes.splice(0, 1)
      // filterNodes.shift()
// console.log(nodes[idx].children)
      filterNodes.length
        && this.checkChangedNodes(nodes[idx].children, filterNodes)
    }
  },
  computed: {
    treeNode () {
      return Object.assign([], this.nodes)
    }
  },
  components: {
    tree: require('./tree.vue')
  }
}
</script>



